@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:100,200,300,regular,500,600,700,800,900);

* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

html,
body {
  height: 100%;
}

body {
  color: #ffffff;
  font-family: "Roboto Slab", serif;
  font-size: 18px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input,
button,
textarea {
  font-family: "Roboto Slab", serif;
  font-size: inherit;
}

textarea {
  resize: none;
}

button {
  cursor: pointer;
  color: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

ul li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.wrapper > main {
  flex: 1 1 auto;
}

.container {
  padding: 0 5%;
}

html,
body {
  scroll-behavior: smooth;
}

body {
  background-color: #1A1A1A;
}

body .wrapper.light {
  background-color: #ffffff;
  color: #000;
}

.header {
  padding: 30px 0;
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  transition: all 0.4s ease;
}

.header.fixed {
  padding: 20px 0;
  background-color: rgba(0, 0, 0, 0.76);
  z-index: 5;
  backdrop-filter: blur(4px);
  color: #ffffff;
}

.header.fixed.show {
  padding: 30px 0 20px;
}

.wrapper.light .header.fixed .theme {
  background-color: #fff;
}

.header__content {
  display: flex;
  justify-content: space-between;
}

.header__logo {
  font-weight: 900;
  font-size: 22px;
  line-height: 26px;
  position: relative;
}

.header__logo span {
  color: #17FF78;
}

.header__logo .switch-lang {
  position: absolute;
  top: -9px;
  right: -32px;
  font-size: 18px;
  cursor: pointer;
  line-height: 110%;
}

.header__logo .switch-lang:hover {
  border-bottom: 1px solid white;
}

.wrapper.light .header__logo .switch-lang:hover {
  border-bottom: 1px solid black;
}

.wrapper.light .header.fixed .header__logo .switch-lang:hover {
  border-bottom: 1px solid white;
}

.header__list {
  display: flex;
}

.header__item {
  text-transform: capitalize;
  font-weight: 900;
  font-size: 20px;
  line-height: 26px;
  transition: all 0.3s ease;
  margin-right: 25px;
}

.header__item.active,
.header__item:hover {
  color: #17FF78;
}

.header__item:last-child {
  margin-right: 0;
}

.header__nav {
  display: flex;
}

.theme {
  width: 28px;
  height: 28px;
  background-color: #292929;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  left: 15px;
  transition: all 0.3s ease;
}

.theme .dark {
  display: block;
  width: 38px;
  position: absolute;
  top: -5px;
  left: -5px;
}

.theme .light {
  display: none;
}

.menu {
  display: none;
}

.wrapper.light .theme {
  background-color: #CDCDCD;
}

.wrapper.light .theme .dark {
  display: none;
}

.wrapper.light .theme .light {
  display: block;
  width: 22px;
  position: absolute;
  top: 3px;
  left: 3px;
}

.wrapper.light .menu path {
  fill: #000;
}

.wrapper.light .header.fixed .menu path {
  fill: #fff;
}

.title {
  padding: 40px 0;
  font-weight: 700;
  font-size: 40px;
  line-height: 63px;
}

.title span {
  color: #17FF78;
}

.page__preview {
  min-height: 100vh;
  background: url("../img/background-preview.png");
  background-size: cover;
  background-attachment: fixed;
}

.wrapper.light .page__preview {
  background: url("../img/background-preview-light.png");
  background-size: cover;
  background-attachment: fixed;
}

.preview__row {
  padding: 180px 0 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.preview__text {
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
}

.preview__title {
  font-weight: 800;
  font-size: 60px;
  line-height: 65px;
  color: #17FF78;
  margin-bottom: 5px;
}

.preview__subtitle {
  font-weight: 400;
  font-size: 36px;
  line-height: 47px;
}

.preview__photo {
  position: relative;
  top: 10px;
  flex: 0 0 400px;
  border-radius: 50%;
  width: 400px;
  height: 400px;
  margin-right: 40px;
}

.preview__photo img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.page__about-me {
  min-height: 100vh;
}

.about-me__row {
  padding: 10% 0 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.about-me__img {
  flex: 0 0 400px;
  border-radius: 20px;
  height: 300px;
  margin-right: 100px;
  transform: translate(50px, -50px);
}

.about-me__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
}

.about-me__img::before {
  content: "";
  width: 400px;
  height: inherit;
  border-radius: 20px;
  border: 3px solid #17FF78;
  transform: translate(-40px, 40px);
  position: absolute;
  z-index: -3;
}

.about-me__text {
  font-weight: 400;
  font-size: 18px;
  line-height: 150.38%;
  letter-spacing: 0.03em;
  margin-bottom: 50px;
}

.about-me__text > a {
  text-decoration: underline;
  color: #17FF78;
}

.about-me__text > a:hover {
  text-decoration: none;
}

.page__skills {
  background-color: #161616;
  padding: 0 0 80px;
}

.wrapper.light .page__skills {
  background-color: #eeeeee;
}

.skills__row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  row-gap: 40px;
}

.skills__block {
  flex: 0 1 14%;
  text-align: center;
}

.skills__block .block__body {
  display: inline-block;
  cursor: pointer;
  width: 100px;
  height: 100px;
  position: relative;
  transition: all 0.3s ease;
}

.skills__block .block__body:hover {
  transform: translateY(-5px);
}

.skills__block .block__body:hover span {
  opacity: 1;
  bottom: -30px;
  z-index: 1;
}

.skills__block .block__body img {
  width: 100%;
  height: 100%;
}

.skills__block .block__body span {
  width: 100%;
  display: block;
  position: absolute;
  bottom: 10px;
  opacity: 0;
  z-index: -1;
  transition: bottom 0.4s ease, opacity 0.5s cubic-bezier(0.25, 0.1, 0.25, 1);
}

.page__projects {
  min-height: 100vh;
  background: url("../img/background-projects.png");
  background-size: cover;
  background-attachment: fixed;
}

.wrapper.light .page__projects {
  background: url("../img/background-projects-light.png");
  background-size: cover;
  background-attachment: fixed;
}

.projects__row {
  padding: 0 0 100px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  row-gap: 35px;
}

.projects__item {
  flex: 0 0 388px;
  height: 230px;
  position: relative;
  border-radius: 20px;
}

.projects__item:hover .item__info {
  opacity: 1;
}

.projects__item .item__image {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  border: 2px solid black;
}

.item__info {
  transition: all 0.3s ease;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7764705882);
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

.item__info .icon-code {
  width: 30px;
  position: absolute;
  top: 15px;
  left: 20px;
  transition: transform 0.3s ease;
}

.item__info .icon-code:hover {
  transform: scale(1.1);
}

.item__info .icon-link {
  width: 30px;
  position: absolute;
  top: 15px;
  right: 20px;
  transition: transform 0.3s ease;
}

.item__info .icon-link:hover {
  transform: scale(1.1);
}

.item__info .info__text {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  background-color: #000;
  padding: 10px 8px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.item__info .info__text span {
  color: #17FF78;
}

.item__image_last {
  width: 100%;
  height: 100%;
  background-color: darkgray;
}

.item__info_last {
  opacity: 1;
}

.item__info_last .info__text_last {
  bottom: 39%;
  background-color: transparent;
  cursor: default;
}

.wrapper.light .item__info {
  color: #ffffff;
}

.footer {
  background-color: #000;
}

.wrapper.light .footer {
  background-color: #f6f6f6;
}

.footer__quote {
  text-align: center;
  margin-bottom: 50px;
}

.footer__quote q {
  font-weight: 300;
  font-style: italic;
  margin-bottom: 10px;
  display: inline-block;
}

.footer__quote cite {
  font-style: normal;
  position: relative;
}

.footer__quote cite::before,
.footer__quote cite::after {
  content: "";
  width: 40px;
  height: 1px;
  background-color: #fff;
  position: absolute;
  top: 12px;
}

.footer__quote cite::after {
  right: -50px;
}

.footer__quote cite::before {
  left: -50px;
}

.wrapper.light cite::before,
.wrapper.light cite::after {
  background-color: #000000;
}

.wrapper.light .footer__bottom {
  border-top: 2px solid #cdcdcd;
}

.wrapper.light .footer__links svg path {
  fill: #000;
}

.footer__links {
  text-align: center;
  margin-bottom: 30px;
}

.footer__links a {
  display: inline-block;
  width: 45px;
  transition: all 0.4s ease;
}

.footer__links a:not(:last-child) {
  margin-right: 30px;
}

.footer__links a img {
  width: 100%;
}

.footer__links .mail {
  position: relative;
  cursor: pointer;
}

.footer__links .mail span {
  cursor: text;
  font-size: 16px;
  position: absolute;
  top: 11px;
  left: 45px;
  transform: scale(0);
  transform-origin: left;
  transition: all 0.5s ease-in-out;
}

.footer__links .mail span.show {
  transform: scale(1);
  left: 65px;
}

.footer__bottom {
  border-top: 2px solid #292929;
  text-align: center;
  padding: 20px 0;
  font-size: 16px;
}

@media (min-width: 1445px) {
  .container {
    max-width: 1445px;
    margin: 0 auto;
  }

  .page__preview {
    min-height: fit-content;
  }

  .page__about-me {
    min-height: fit-content;
  }

  .page__projects {
    min-height: fit-content;
  }
}

@media (max-width: 930px) {
  .header__logo {
    font-size: 20px;
  }

  .header__logo .switch-lang {
    position: absolute;
    top: 4px;
    right: -33px;
    font-size: 16px;
    cursor: pointer;
    line-height: 110%;
  }

  .header__list {
    position: absolute;
    top: -50px;
    left: 0;
    width: 100%;
    justify-content: center;
    transition: all 0.4s ease;
  }

  .header__list.show {
    top: 3px;
  }

  .header__item {
    font-size: 18px;
    line-height: 110%;
  }

  .header__item:hover {
    color: #ffffff;
  }

  .header__item.active {
    color: #17FF78;
  }

  .theme {
    left: 0;
    margin-right: 15px;
  }

  .menu {
    display: block;
    width: 26px;
    height: 26px;
    cursor: pointer;
  }

  .title {
    font-size: 35px;
  }

  .page__preview {
    min-height: fit-content;
  }

  .preview__row {
    padding: 120px 0 180px;
    flex-direction: column-reverse;
    align-items: self-start;
  }

  .preview__photo {
    align-self: center;
    top: 0;
    margin-right: 0;
    flex: 0 0 300px;
    width: 300px;
    height: 300px;
    margin-bottom: 20px;
  }

  .preview__text {
    font-size: 22px;
  }

  .preview__title {
    font-size: 55px;
  }

  .preview__subtitle {
    font-size: 32px;
  }

  .page__about-me {
    min-height: fit-content;
  }

  .about-me__img {
    display: none;
  }

  .about-me__row {
    padding: 2% 0 100px;
  }

  .skills__block .block__body {
    width: 75px;
    height: 75px;
  }

  .skills__block .block__body:hover span {
    bottom: -25px;
  }

  .skills__block .block__body span {
    font-size: 14px;
  }

  .page__projects {
    min-height: fit-content;
  }

  .page__projects > .container {
    padding: 0;
  }

  .projects__title {
    padding: 40px 5%;
  }

  .projects__row {
    overflow-x: scroll;
    overflow-y: hidden;
    -ms-overflow-style: none;
    padding: 0 5% 100px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: nowrap;
    column-gap: 30px;
  }

  .projects__row::-webkit-scrollbar {
    width: 0;
  }

  .projects__row::-webkit-scrollbar-track,
  .projects__row::-webkit-scrollbar-thumb {
    background-color: transparent;
  }

  .footer__links .mail span {
    top: 35px;
    left: -89px;
    transform: scale(0);
    transform-origin: top;
    transition: all 0.5s ease-in-out;
  }

  .footer__links .mail span.show {
    transform: scale(1);
    left: -89px;
    top: 55px;
  }
}

@media (max-width: 630px) {
  .header__logo {
    font-size: 18px;
  }

  .header__item {
    font-size: 14px;
    margin-right: 10px;
  }

  .title {
    font-size: 30px;
    padding: 20px 0;
  }

  .preview__row {
    padding: 120px 0 100px;
  }

  .preview__text {
    font-size: 20px;
  }

  .preview__title {
    font-size: 30px;
    line-height: 40px;
    margin-bottom: 0;
  }

  .preview__subtitle {
    font-size: 24px;
  }

  .preview__photo {
    flex: 0 0 230px;
    width: 230px;
    height: 230px;
  }

  .about-me__row {
    padding: 2% 0 50px;
  }

  .about-me__text {
    font-size: 16px;
  }

  .about-me__body button a {
    font-size: 18px;
    padding: 14px 35px;
  }

  .page__skills {
    padding: 0 0 40px;
  }

  .skills__row {
    row-gap: 30px;
  }

  .skills__block .block__body {
    width: 55px;
    height: 55px;
    margin-right: 5px;
  }

  .skills__block .block__body span {
    font-size: 12px;
  }

  .projects__row {
    column-gap: 15px;
    padding: 0 5% 50px;
  }

  .projects__title {
    padding: 20px 5%;
  }

  .projects__item {
    flex: 0 0 290px;
    height: 190px;
  }

  .footer__quote {
    margin-bottom: 30px;
  }

  .footer__quote q {
    font-size: 16px;
  }

  .footer__quote cite {
    font-size: 16px;
  }

  .footer__links a {
    width: 35px;
  }

  .footer__links .mail span {
    font-size: 14px;
  }

  .footer__links .mail span.show {
    transform: scale(1);
    left: -151px;
    top: 48px;
  }

  .footer__bottom {
    font-size: 14px;
  }
}

@media (max-width: 375px) {
  .header__list {
    flex-wrap: wrap;
  }

  .header__item_ru {
    margin-bottom: 5px;
  }

  .header-ru.show.fixed,
  .header-ru.show {
    padding: 40px 0 20px;
  }
}