* {
   padding: 0px;
   margin: 0px;
   border: 0px;
}

*,
*:before,
*:after {
   box-sizing: border-box;
}

:focus,
:active {
   outline: none;
}

a:focus,
a:active {
   outline: none;
}

html,
body {
   height: 100%;
}

body {
   color: $textColor;
   font-family: $fontFamily;
   font-size: $fontSize;
   //text-rendering: optimizeLegibility;
   -ms-text-size-adjust: 100%;
   -moz-text-size-adjust: 100%;
   -webkit-text-size-adjust: 100%;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}

input,
button,
textarea {
   font-family: $fontFamily;
   font-size: inherit;
}

textarea {
   resize: none;
}

button {
   cursor: pointer;
   color: inherit;
}

a {
   color: inherit;
   text-decoration: none;
}

ul li {
   list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
   font-weight: inherit;
   font-size: inherit;
}