@import 'vars';

.footer {
   background-color: #000;
}

.wrapper.light {
   .footer {
      background-color: #f6f6f6;
   }
}

.footer__quote {
   text-align: center;
   margin-bottom: 50px;

   q {
      font-weight: 300;
      font-style: italic;
      margin-bottom: 10px;
      display: inline-block;
   }

   cite {
      font-style: normal;
      position: relative;

      &::before,
      &::after {
         content: '';
         width: 40px;
         height: 1px;
         background-color: #fff;
         position: absolute;
         top: 12px;
      }

      &::after {
         right: -50px;
      }

      &::before {
         left: -50px;
      }
   }
}

.wrapper.light {
   cite {

      &::before,
      &::after {
         background-color: #000000;
      }
   }

   .footer__bottom {
      border-top: 2px solid #cdcdcd;
   }

   .footer__links {
      svg {
         path {
            fill: #000;
         }
      }
   }
}

.footer__links {
   text-align: center;
   margin-bottom: 30px;

   a {
      display: inline-block;
      width: 45px;
      transition: all 0.4s ease;

      &:not(:last-child) {
         margin-right: 30px;
      }

      img {
         width: 100%;
      }
   }

   .mail {
      position: relative;
      cursor: pointer;

      span {
         cursor: text;
         font-size: 16px;
         position: absolute;
         top: 11px;
         left: 45px;
         transform: scale(0);
         transform-origin: left;
         transition: all 0.5s ease-in-out;

         &.show {
            transform: scale(1);
            left: 65px;
         }
      }
   }
}

.footer__bottom {
   border-top: 2px solid #292929;
   text-align: center;
   padding: 20px 0;
   font-size: 16px;
}

@media (max-width: 930px) {
   .footer__links {
      .mail {
         span {
            top: 35px;
            left: -89px;
            transform: scale(0);
            transform-origin: top;
            transition: all 0.5s ease-in-out;

            &.show {
               transform: scale(1);
               left: -89px;
               top: 55px;
            }
         }
      }
   }
}

@media (max-width: 630px) {
   .footer__quote {
      margin-bottom: 30px;

      q {
         font-size: 16px;
      }

      cite {
         font-size: 16px;
      }
   }

   .footer__links {
      a {
         width: 35px;
      }

      .mail {
         span {
            font-size: 14px;

            &.show {
               transform: scale(1);
               left: -151px;
               top: 48px;
            }
         }
      }
   }

   .footer__bottom {
      font-size: 14px;
   }
}